import store from '../stores';

export function getParams(param) {
  return {
    ...param,
  }
}

export function getBody(param) {
  return {
    ...param,
  }
}

export function getHeaders() {
  return {
    Authorization: `Bearer ${window.localStorage.getItem('token')}`,
  }
}

export function createdBy(param) {
  return {
    ...param,
    creator: store.authStore.user.id
  }
}

export function updatedBy(param) {
  return {
    ...param,
    updater: store.authStore.user.id
  }
}