import { action, computed } from "mobx";
import Apis from "../apis";
window.permissions = {}; //角色权限

class AuthStore {
  // @observable permissions = {}; //角色权限

  @computed
  get user() {
    let _user = window.localStorage.getItem("user");
    try {
      return _user && _user !== "undefined" ? JSON.parse(_user) : {};
    } catch (err) {
      console.error(err);
      return {};
    }
  }

  @computed
  get token() {
    let _token = window.localStorage.getItem("token");
    return _token && _token !== "undefined" ? _token : "";
  }

  // 权限信息
  @action getPermission = (callback) => {
    if (this.user.role && this.user.role.id) {
      Apis.authPermission({ role: this.user.role.id }).then((res) => {
        if (res && res.permissions) {
          window.permissions = res.permissions;
          if (callback) callback();
        }
      });
    }
  };
  // 登录
  @action login = (params, callback, err) => {
    Apis.login(params).then((res) => {
      const { jwt, user } = res || {};
      if (jwt) {
        if (window.localStorage) {
          window.localStorage.setItem("token", jwt);
          window.localStorage.setItem("user", JSON.stringify(user));
          setTimeout(() => {
            if (callback) callback();
          }, 1000);
        }
      } else {
        if (err) err(res.msg);
      }
    });
  };
  // 登出
  @action logout = (callback) => {
    // Apis.logout({ username: this.user.username }).then(res => {
    if (window.localStorage) {
      window.localStorage.removeItem("token");
      window.localStorage.removeItem("user");
      if (callback) callback();
    }
    // })
  };

}

export default AuthStore;
