// 路由页面配置
import LoadComponent from './loadable';

const routers = [{
  path: "/",
  exact: true,
  redirect: "/index",
}, {
  path: "/login",
  exact: true,
  component: LoadComponent(() => import('@/pages/login'/* webpackChunkName: "login" */))
}, {
  path: "/index",
  component: LoadComponent(() => import('@/pages/'/* webpackChunkName: "pages" */)),
}]
export default routers