/**
 * 基础接口
 */
import CONFIG from '@/config'
import http from '@components/utils/http'
import { getParams, getBody, getHeaders, createdBy, updatedBy } from "./common";
const { service } = CONFIG.ROOTURI

const apis = {
  // app
  appCount(param, tbName) {
    return http.get(`${service}/${tbName}/count`, getParams(param), getHeaders())
  },
  appList(param, tbName) {
    return http.get(`${service}/${tbName}`, getParams(param), getHeaders())
  },
  appDetail(param, tbName) {
    let { _id, ...data } = getParams(param)
    return http.get(`${service}/${tbName}/${_id}`, data, getHeaders())
  },
  appCreate(param, tbName) {
    let { _id, ...data } = getBody(param)
    return http.post(`${service}/${tbName}`, createdBy(data), getHeaders())
  },
  appUpdate(param, tbName) {
    let { _id, ...data } = getBody(param)
    return http.put(`${service}/${tbName}/${_id}`, updatedBy(data), getHeaders())
  },
  appRemove(param, tbName) {
    let { _id, ...data } = getParams(param)
    return http.delete(`${service}/${tbName}/${_id}`, data, getHeaders())
  },
  // 页面个性化
  getPageCustomizations(param) {
    return http.get(`${service}/page-customizations`, getParams(param), getHeaders())
  },
  putPageCustomizations(param) {
    let { _id, ...data } = getBody(param)
    if (_id) {
      return http.put(`${service}/page-customizations/${_id}`, data, getHeaders())
    } else {
      return http.post(`${service}/page-customizations`, data, getHeaders())
    }
  },
  customizeFuns(param) {
    return http.get(`${service}/customize-funs`, getParams(param), getHeaders())
  },
  createCustomizeFuns(param) {
    let { _id, ...data } = getBody(param)
    return http.post(`${service}/customize-funs`, createdBy(data), getHeaders())
  },
  updateCustomizeFuns(param) {
    let { _id, ...data } = getBody(param)
    return http.put(`${service}/customize-funs/${_id}`, updatedBy(data), getHeaders())
  },
  dictionarys(param) {
    return http.get(`${service}/dictionarys`, getParams(param), getHeaders())
  },
  // 更新页面配置
  updateViewData(param) {
    let { _id, ...data } = getBody(param)
    return http.put(`${service}/views/${param._id}`, updatedBy(data), getHeaders())
  },
  // 数据表字段信息
  contentTypes(param) {
    return http.get(`${service}/content-manager/content-types`,
      getParams(param), getHeaders())
  },
  contentTypesConfig(name) {
    return http.get(`${service}/content-manager/content-types/application::${name}.${name}/configuration`,
      getParams({}), getHeaders())
  }
}

export default apis