import { observable, action, } from "mobx";
import Apis from "../apis";
import store from './index'

class AppStore {
  @observable activeKey = 0;
  @observable panes = [];
  @observable setting = {
    pageSize: 20,
  };
  @observable pageSettings = {}

  // 选项卡
  @action setActiveKey = (key) => {
    if (key) this.activeKey = key;
    window.location.hash = `#${key || this.activeKey}`;
  };

  @action addTab = ({ url, title, module }) => {
    let hasOne = this.panes.find((item) => item.module === module);
    if (!hasOne) {
      this.panes.push({
        url,
        title,
        module,
      });
    } else {
      if (url) hasOne.url = url;
      if (title) hasOne.title = title;
    }
    this.setActiveKey(url);
    window.location.hash = `#${url}`;
  };

  @action removeTab = (targetKey) => {
    if (!targetKey) targetKey = this.activeKey;
    let index = 0;
    this.panes.forEach((item, i) => {
      if (this.activeKey === item.url) {
        index = i - 1;
      }
    });
    this.panes = this.panes.filter((pane) => pane.url !== targetKey);
    if (this.panes.length) {
      if (index >= 0) {
        this.activeKey = this.panes[index].url;
      } else {
        this.activeKey = this.panes[0].url;
      }
    }
    window.location.hash = `#${this.activeKey}?v=${+new Date()}`;
  };

  @action getMenu = async () => {
    let res = await Apis.menuList({ _limit: 100 });
    if (Array.isArray(res)) {
      function loop(pid) {
        let children = res.filter((val) => val.pId === pid) || [];
        children.forEach((item) => {
          item.id = item._id;
          let _res = loop(item._id);
          _res.sort((a, b) => {
            return a.sortIndex - b.sortIndex;
          });
          if (_res.length) item.children = _res;
        });
        children.sort((a, b) => {
          return a.sortIndex - b.sortIndex;
        });
        return children;
      }
      return loop("");
    }
  };

  // 配置信息
  @action getSetting = () => {
    Apis.settingDetail().then(res => {
      if (res) {
        let { _id, published_at, createdAt, updatedAt, __v, id, ...data } = res;
        this.setting = data;
      }
    });
  };

  // 页面配置信息
  @action requestPageSetting = async url => {
    const uid = store.authStore && store.authStore.user ? store.authStore.user.id : null
    if (!uid) return
    if (!url) url = window.location.hash
    let res = await Apis.getPageCustomizations({ uid, url });
    if (!res.statusCode && Array.isArray(res)) {
      this.pageSettings[url] = res[0];
    }
    return this.pageSettings[url]
  };

}

export default AppStore;
