import React from "react";
import Apis from "../apis";
// import { observable, action, } from "mobx";
import * as funs from "@/components/utils/fun";
window.variables = {};
window.funs = funs

class GlobalStore {
  // @observable collapsed = false;
  constructor() {
    this.getVariables();
  }

  formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 5 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 14 },
    },
  };

  formTailLayout = {
    wrapperCol: {
      xs: {
        span: 24,
        offset: 0,
      },
      sm: {
        span: 14,
        offset: 5,
      },
    },
  };

  // 预加载变量数据
  getVariables = async (param) => {
    let res = await Apis.variablesList();
    if (Array.isArray(res)) {
      for (let item of res) {
        let arr = item.fieldName.split(","),
          params = funs.queryToObj(item.urlQuery);
        params._sort = "createdAt:desc";
        if (!params._limit) params._limit = 100
        let result = await Apis.appList(params, item.tbName);
        if (result) {
          result = result.map((val) => {
            let obj = {};
            for (let str of arr) {
              obj[str] = val[str];
            }
            return obj;
          });
          window.variables[item.name] = item.type === "list" ? result : result[0];
        }
      }
    }
  };

  // 执行自定义函数
  execFun = (obj, ...params) => {
    if (!obj) return null;
    let { outputType = "", inputParams = "", body, type } = obj;
    let paramsArr = inputParams ? inputParams.split(",") : ["value"];
    if (type === "table_column") paramsArr = ["value", "record", "index", 'global'];
    if (type === "input_value") paramsArr = ["value", "record", 'global'];
    if (type === "form_data") paramsArr = ["value", "record", 'global'];
    try {
      let result = paramsArr[0]
      if (body) {
        // eslint-disable-next-line
        result = new Function(...paramsArr, body)(...params, window.variables);
      }
      switch (outputType) {
        case "string":
          return `${result}`;
        case "number":
          return Number(result);
        case "boolean":
          return !!result;
        case "array":
          return Array.isArray(result) ? result : [result];
        case "object":
          return !Array.isArray(result) && typeof result === 'object' ? result : {};
        case "html":
          return <div dangerouslySetInnerHTML={{ __html: `${result}` }}></div>;
        default:
          return `${result}`;
      }
    } catch (err) {
      console.error(err);
      return null;
    }
  };

}

export default GlobalStore;
