import React, { Component, Fragment } from "react";
import { HashRouter as Router, Route, Redirect, } from "react-router-dom";
import { Modal } from "antd";
import routesData from "./config";

function loop(routes) {
  return routes.map((route, key) => {
    if (route.redirect) {
      return (
        <Route
          key={key}
          path={route.path}
          exact={route.exact}
          render={() => <Redirect to={route.redirect} />}
        />
      );
    } else {
      return (
        <Route
          key={key}
          path={route.path}
          exact={route.exact}
          render={(props) => (
            <route.component {...props} routes={loop(route.children || [])} />
          )}
        />
      );
    }
  });
}

class App extends Component {
  constructor() {
    super()
    window.addEventListener("hashchange", () => {
      Modal.destroyAll();
    });
  }
  render() {
    return (
      <Router>
        <Fragment>{loop(routesData)}</Fragment>
      </Router>
    );
  }
}
export default App;
