import axios from "axios";
import qs from "qs";

function interceptors(instance, url) {
  // 请求拦截
  instance.interceptors.request.use(
    (req) => {
      return req;
    },
    (error) => {
      throw error;
    }
  );
  // 响应拦截
  instance.interceptors.response.use(
    (res) => {
      return res;
    },
    (error) => {
      throw error;
    }
  );
}

function request(options) {
  const instance = axios.create({
    // baseURL: "/api",
    // timeout: 3000,
  });
  options = Object.assign(
    {
      // baseURL: this.baseUrl,
    },
    options
  );
  interceptors(instance, options.url);
  return instance(options);
}

function getHttp(url) {
  if (url.indexOf("http") >= 0 || url.indexOf("/") === 0) {
    return url;
  } else {
    return (
      (document.location.protocol === "https:" ? "https" : "http") + "://" + url
    );
  }
}

const Ajax = async (method, url, params, headers) => {
  let paramsObj = {
    headers: headers ? headers : { "content-type": "application/json" },
    url: getHttp(url),
    withCredentials: true,
    method: method,
  };
  if (method === "GET") {
    paramsObj.url = `${paramsObj.url}?${qs.stringify(params)}`;
  } else {
    paramsObj.data = params;
  }
  try {
    let res = await request(paramsObj);
    return res.data;
  } catch (err) {
    const { status, data } = err.response
    if (status === 401) {
      window.location.href = "#/login";
    } else {
      return { status, error: data.error, message: data.message }
    }
  }
};

export default class Http {
  // get 方法封装
  static get(url, params = {}, headers) {
    return Ajax("GET", url, params, headers);
  }

  // post 方法封装
  static post(url, params = {}, headers) {
    return Ajax("POST", url, params, headers);
  }

  // put 方法封装
  static put(url, params = {}, headers) {
    return Ajax("PUT", url, params, headers);
  }

  // delete 方法封装
  static delete(url, params = {}, headers) {
    return Ajax("DELETE", url, params, headers);
  }
}
