import http from '@components/utils/http'
import CONFIG from '@/config'
import { getParams, getBody, getHeaders, createdBy, updatedBy } from "./common";
const { service } = CONFIG.ROOTURI

const apis = {
  // 菜单
  menuList(param) {
    return http.get(`${service}/menus`, getParams(param), getHeaders())
  },
  menuCreate(param) {
    let { _id, ...data } = getBody(param)
    return http.post(`${service}/menus`, createdBy(data), getHeaders())
  },
  menuUpdate(param) {
    let { _id, ...data } = getBody(param)
    return http.put(`${service}/menus/${_id}`, updatedBy(data), getHeaders())
  },
  menuRemove(param) {
    let { _id, ...data } = getParams(param)
    return http.delete(`${service}/menus/${_id}`, data, getHeaders())
  },
  // 字典
  dictionaryList(param) {
    return http.get(`${service}/dictionarys`, getParams(param), getHeaders())
  },
  async dictionaryOptions(param) {
    let { _id, ...data } = getParams(param)
    let detail = await http.get(`${service}/dictionarys/${_id}`, data, getHeaders())
    return detail ? detail.options : []
  },
  dictionaryCreate(param) {
    let { _id, ...data } = getBody(param)
    return http.post(`${service}/dictionarys`, createdBy(data), getHeaders())
  },
  dictionaryUpdate(param) {
    let { _id, ...data } = getBody(param)
    return http.put(`${service}/dictionarys/${_id}`, updatedBy(data), getHeaders())
  },
  dictionaryRemove(param) {
    let { _id, ...data } = getParams(param)
    return http.delete(`${service}/dictionarys/${_id}`, data, getHeaders())
  },
  // 设置
  settingDetail(param) {
    return http.get(`${service}/setting`, getParams(param), getHeaders())
  },
  // 日志
  logList(param) {
    return http.get(`${service}/logs`, getParams(param), getHeaders())
  },
  logDetail(param) {
    let { _id, ...data } = getParams(param)
    return http.get(`${service}/logs/${_id}`, data, getHeaders())
  },
  logRemove(param) {
    let { _id, ...data } = getParams(param)
    return http.delete(`${service}/logs/${_id}`, data, getHeaders())
  },
  // 类别
  categoryList(param) {
    return http.get(`${service}/categorys`, getParams(param), getHeaders())
  },
  categoryCreate(param) {
    let { _id, ...data } = getBody(param)
    return http.post(`${service}/categorys`, createdBy(data), getHeaders())
  },
  categoryUpdate(param) {
    let { _id, ...data } = getBody(param)
    return http.put(`${service}/categorys/${_id}`, updatedBy(data), getHeaders())
  },
  categoryRemove(param) {
    let { _id, ...data } = getParams(param)
    return http.delete(`${service}/categorys/${_id}`, data, getHeaders())
  },
  // 视图
  viewList(param) {
    return http.get(`${service}/views`, getParams(param), getHeaders())
  },
  //自定义变量
  async variablesList(param = {}) {
    return http.get(`${service}/variables`, getParams(param), getHeaders())
  },
}

export default apis