import moment from "moment";
moment.locale("zh-cn");

// 验证权限
export function permissions(key, action) {
  if (key.indexOf('application.controllers.') < 0 && action) {
    key = `application.controllers['${key}'].${action}.enabled`
  }
  try {
    if (key) {
      // eslint-disable-next-line
      let result = new Function(`return window.permissions.${key}`)();
      return result
    } else {
      return false;
    }
  } catch (err) {
    return false;
  }
}

// 深拷贝
export function deepCopy(obj) {
  let newobj = Array.isArray(obj) ? [] : {};
  if (typeof obj !== "object") {
    return;
  } else {
    for (let i in obj) {
      newobj[i] =
        typeof obj[i] === "object" && !(obj[i] instanceof Date)
          ? deepCopy(obj[i])
          : obj[i];
    }
  }
  return newobj;
}

// 生成uuid
export function uuid() {
  function S4() {
    return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
  }
  return (S4() + S4() + "-" + S4() + "-" + S4() + "-" + S4() + "-" + S4() + S4() + S4());
}

// 是否空对象
export function isEmpty(value = {}) {
  if (typeof value === "object") {
    if (Array.isArray(value)) {
      return !value.length;
    } else {
      let arr = Object.keys(value);
      return !arr.length;
    }
  }
  return false;
}

//数组转树型
export function arrToTree(data, pid) {
  let children = data.filter((val) => val.pId === pid) || [];
  children.forEach((item) => {
    item.id = item._id;
    item.key = item._id;
    item.title = item.title || item.name;
    item.label = item.title || item.name;
    item.value = item._id;
    let _res = arrToTree(data, item._id);
    if (_res.length) item.children = _res;
  });
  children.sort((a, b) => {
    return a.sortIndex - b.sortIndex;
  });
  return children;
}

// 格式化时间
export function formatDate(str, isLong) {
  return moment(new Date(str)).format(
    isLong ? "YYYY-MM-DD HH:mm:ss" : "YYYY-MM-DD"
  );
}

export function fixedZero(val) {
  return val * 1 < 10 ? `0${val}` : val;
}

export function getTimeDistance(type) {
  const now = new Date();
  const oneDay = 1000 * 60 * 60 * 24;
  if (type === "today") {
    now.setHours(0);
    now.setMinutes(0);
    now.setSeconds(0);
    return [moment(now), moment(now.getTime() + (oneDay - 1000))];
  }
  if (type === "week") {
    let day = now.getDay();
    now.setHours(0);
    now.setMinutes(0);
    now.setSeconds(0);
    if (day === 0) {
      day = 6;
    } else {
      day -= 1;
    }
    const beginTime = now.getTime() - day * oneDay;
    return [moment(beginTime), moment(beginTime + (7 * oneDay - 1000))];
  }
  if (type === "month") {
    const year = now.getFullYear();
    const month = now.getMonth();
    const nextDate = moment(now).add(1, "months");
    const nextYear = nextDate.year();
    const nextMonth = nextDate.month();
    return [
      moment(`${year}-${fixedZero(month + 1)}-01 00:00:00`),
      moment(
        moment(
          `${nextYear}-${fixedZero(nextMonth + 1)}-01 00:00:00`
        ).valueOf() - 1000
      ),
    ];
  }
  if (type === "year") {
    const year = now.getFullYear();
    return [moment(`${year}-01-01 00:00:00`), moment(`${year}-12-31 23:59:59`)];
  }
}

export function digitUppercase(n) {
  const fraction = ["角", "分"];
  const digit = ["零", "壹", "贰", "叁", "肆", "伍", "陆", "柒", "捌", "玖"];
  const unit = [
    ["元", "万", "亿"],
    ["", "拾", "佰", "仟"],
  ];
  let num = Math.abs(n);
  let s = "";
  fraction.forEach((item, index) => {
    s += (digit[Math.floor(num * 10 * 10 ** index) % 10] + item).replace(
      /零./,
      ""
    );
  });
  s = s || "整";
  num = Math.floor(num);
  for (let i = 0; i < unit[0].length && num > 0; i += 1) {
    let p = "";
    for (let j = 0; j < unit[1].length && num > 0; j += 1) {
      p = digit[num % 10] + unit[1][j] + p;
      num = Math.floor(num / 10);
    }
    s = p.replace(/(零.)*零$/, "").replace(/^$/, "零") + unit[0][i] + s;
  }
  return s
    .replace(/(零.)*零元/, "元")
    .replace(/(零.)+/g, "零")
    .replace(/^整$/, "零元整");
}

export function isUrl(path) {
  const reg = /^https?:(?:\/\/)?$/g;
  return reg.test(path);
}

/**
 * 时间戳格式化
 * @timestamp  时间戳
 * @format  日期格式 例：yyyy-MM-dd hh:mm:ss
 * @return yyyy-MM-dd hh:mm:ss
 */
export function formatDateTime(date, fmt) {
  if (!date) return "";
  let ret;
  if (typeof date === "string") date = new Date(date);
  const opt = {
    "Y+": date.getFullYear().toString(), // 年
    "m+": (date.getMonth() + 1).toString(), // 月
    "d+": date.getDate().toString(), // 日
    "H+": date.getHours().toString(), // 时
    "M+": date.getMinutes().toString(), // 分
    "S+": date.getSeconds().toString(), // 秒
    "q+": Math.floor((date.getMonth() + 3) / 3), //季度
    // 有其他格式化字符需求可以继续添加，必须转化成字符串
  };
  for (let k in opt) {
    ret = new RegExp("(" + k + ")").exec(fmt);
    if (ret) {
      fmt = fmt.replace(
        ret[1],
        ret[1].length === 1 ? opt[k] : opt[k].padStart(ret[1].length, "0")
      );
    }
  }
  return fmt;
}
/**
 * 将以base64的图片转为file
 */
export function dataURLtoFile(dataurl, filename) {
  let arr = dataurl.split(","),
    mime = arr[0].match(/:(.*?);/)[1],
    bstr = atob(arr[1]),
    n = bstr.length,
    u8arr = new Uint8Array(n);
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new File([u8arr], filename, { type: mime });
}

/**
 * 判断是否为base64
 * @param {*} str
 */
export function isBase64(str) {
  if (str === "" || str.trim() === "") {
    return false;
  }
  try {
    return btoa(atob(str)) === str;
  } catch (err) {
    return false;
  }
}

/**
 * 获取url参数
 */
export function getUrlParam(name) {
  let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"),
    queryStr =
      window.location.search.substr(1) || window.location.hash.substr(1),
    index = queryStr.indexOf("?"),
    strValue = "";
  if (index) queryStr = queryStr.substr(index + 1);
  let result = queryStr.match(reg);
  if (result != null) {
    strValue = unescape(result[2]);
  }
  return strValue;
}
/**
 * 获取url参数
 */
export function getUrlNode(index) {
  let queryStr = (
    window.location.search.substr(1) || window.location.hash.substr(1)
  ).replace(/#/g, "");
  queryStr = queryStr.indexOf("/") === 0 ? queryStr.substr(1) : queryStr;
  let queryArr = queryStr.split("?");
  let arr = queryArr[0].split("/");
  return arr[index];
}

export function getHeaders() {
  return {
    Authorization: `${window.localStorage.getItem("token")}`,
  };
}

export function filterObj(obj, fun) {
  if (typeof obj === "object" && !Array.isArray(obj)) {
    let result = {};
    Object.keys(obj).forEach((key) => {
      if (typeof fun === "function" && fun(obj[key])) {
        result[key] = obj[key];
      }
    });
    return result;
  } else {
    throw new Error("参数格式不正确");
  }
}

export function htmlEncode(str) {
  if (str.length === 0) return "";
  str = str.replace(/&/g, "&amp;");
  str = str.replace(/</g, "&lt;");
  str = str.replace(/>/g, "&gt;");
  str = str.replace(/ /g, "&nbsp;");
  str = str.replace(/'/g, "&#39;");
  str = str.replace(/"/g, "&quot;");
  return str;
}

export function htmlDecode(str) {
  if (str.length === 0) return "";
  str = str.replace(/&amp;/g, "&");
  str = str.replace(/&lt;/g, "<");
  str = str.replace(/&gt;/g, ">");
  str = str.replace(/&nbsp;/g, " ");
  str = str.replace(/&#39;/g, "'");
  str = str.replace(/&quot;/g, '"');
  return str;
}

// 首字母大写
export function firstToUpper(str) {
  return str.toLowerCase().replace(/( |^)[a-z]/g, (L) => L.toUpperCase());
}

// param to body
export function queryToObj(str = "") {
  let strArr = str.split("&"),
    result = {};
  for (let key of strArr) {
    let arr = key.split("=");
    result[arr[0]] = arr[1];
  }
  return result;
}

// strToArray
export function strToArray(str = "") {
  let strArr = str.split("\n"),
    result = [];
  for (let key of strArr) {
    let arr = key.split(":");
    result.push({
      text: arr[0],
      value: arr[1],
    });
  }
  return result;
}

// dataType
export function dataType(val, type) {
  switch (type) {
    case 'string':
      return `${val}`
    case 'number':
      return Number(val)
    case 'boolean':
      return !!val
    default:
      return val
  }
}

export function tableName(val) {
  return `${val}${val.substr(-1) === 's' ? '' : 's'}`
}