import http from '@components/utils/http'
import { getParams, getHeaders } from "./common";
import CONFIG from '@/config'
const { service } = CONFIG.ROOTURI

const apis = {
  // 组织
  organizationList(param) {
    return http.get(`${service}/organization/list`, {...param}, getHeaders())
  },
  organizationCreate(param) {
    return http.post(`${service}/organization/create`, {...param}, getHeaders())
  },
  organizationUpdate(param) {
    return http.put(`${service}/organization/update`, {...param}, getHeaders())
  },
  organizationRemove(param) {
    return http.delete(`${service}/organization/remove`, {...param}, getHeaders())
  },
  // 帐号
  authList(param) {
    return http.get(`${service}/auth/list`, getParams(param), getHeaders())
  },
  authDetail(param) {
    return http.get(`${service}/auth/detail`, getParams(param), getHeaders())
  },
  authCreate(param) {
    return http.post(`${service}/auth/create`, getParams(param), getHeaders())
  },
  authUpdate(param) {
    return http.put(`${service}/auth/update`, getParams(param), getHeaders())
  },
  authRemove(param) {
    return http.delete(`${service}/auth/remove`, getParams(param), getHeaders())
  },
  // 用户
  userList(param) {
    return http.get(`${service}/user/list`, getParams(param), getHeaders())
  },
  userDetail(param) {
    return http.get(`${service}/user/detail`, getParams(param), getHeaders())
  },
  userCreate(param) {
    return http.post(`${service}/user/create`, getParams(param), getHeaders())
  },
  userUpdate(param) {
    return http.put(`${service}/user/update`, getParams(param), getHeaders())
  },
  userRemove(param) {
    return http.delete(`${service}/user/remove`, getParams(param), getHeaders())
  },
  // 角色
  roleList(param) {
    return http.get(`${service}/role/list`, getParams(param), getHeaders())
  },
  roleDetail(param) {
    return http.get(`${service}/role/detail`, getParams(param), getHeaders())
  },
  rolePermissions(param) {
    return http.get(`${service}/role/permissions`, getParams(param), getHeaders())
  },
  roleCreate(param) {
    return http.post(`${service}/role/create`, getParams(param), getHeaders())
  },
  roleUpdate(param) {
    return http.put(`${service}/role/update`, getParams(param), getHeaders())
  },
  roleRemove(param) {
    return http.delete(`${service}/role/remove`, getParams(param), getHeaders())
  },
}

export default apis