/**
 * 基础接口
 */
import CONFIG from '@/config'
import http from '@components/utils/http'
import { getHeaders } from "./common";
const { service } = CONFIG.ROOTURI

const apis = {

  // 获取 七牛云token
  getQiniuToken(params) {
    return http.put(CONFIG.QINIU.token_url, params, getHeaders())
  },

  // 登录
  login(params) {
    return http.post(`${service}/auth/local`, params)
  },
  
  logout(params) {
    return http.delete(`${service}/auth/logout`, params, getHeaders())
  },

  // 注册
  register(params) {
    return http.post(`${service}/auth/register`, params)
  },

  // 邮箱验证码
  mailVerifCode(params) {
    return http.post(`${service}/auth/mailverifcode`, params)
  },

  // 忘记密码
  forgotPassword(params) {
    return http.post(`${service}/auth/forgot_password`, params)
  },

  // 权限
  authPermission(params) {
    return http.get(`${service}/users-permissions/permissions`, params, getHeaders())
  },
}

export default apis